import { request,getToken,Token } from '../../request.js'
import Loading from '../../common/loading/loading.vue'



export default {
	data() {
		return {
			location: '',
			baseURL:this.$api,
			imgUrl: [],
			shop_name:'',
			shop_address:'',
			shop_phone:'',
			goods:[],
			QRcodeUrl:'123',
			$api: '',
			qr:'',
			isLogin: '',
			// 储存预览店铺图片的路径
			shopSrc: [],
			flag: false,
			// 店铺经纬度
			latitude:'',
			longitude: '',
			shopImg: true,
			swiperShow: true,
			scrollTop: 0
		};
	},
	components: {
		Loading,



	},
	onShow() {
		this.getUserInfo();
	},
	onLoad(options) {
		this.shop_id = options.id;
		this.getUserInfo();
		this.checkLogin()
	},





	mounted() {
		// 获取要预览图片的路径
		if(this.shopImg) {
			setTimeout(() => {
				this.shopSrc.push(this.$refs.shopImg.src);
			},2000)
		}
		
			
		
	},
	methods: {
		// 店铺二维码读取失败事件
		imgLoadError() {
			this.shopImg = false
		},
		// 打开地图
		getLocation() {
			if(this.latitude=='' || this.location == '') {
				uni.showToast({
					title: '该商家暂未提供经纬度',
					icon: 'none'
				})
			}
			else {
				uni.getLocation({
					type: 'wgs84 ',
					success: res => {
						uni.openLocation({
							latitude: res.latitude,
							longitude:res.longitude,
							name: this,shop_address
						})
					}
				});
			}
			
		},
		// 检测登录
		checkLogin() {
			request({
				url:"index.php?s=/wap/member/checkLogin",
				method:"POST",
			}).then(res => {
				if(res.data.code == 201) {
					this.isLogin = false
				}
				else {
					this.isLogin = true
				}
			})
		},
		//获取信息
		getUserInfo() {
			let that = this;
			request({
					url:"index.php?s=/wap/shop/apiIndex",
					method:"POST",
					data: {
						shop_id: this.shop_id,
					}
				}).then(function(res){
					console.log(res);
					that.flag = true;
					that.shop_name = res.data.shop_info.shop_name;
					that.shop_address = res.data.shop_info.live_store_address;
					that.shop_phone = res.data.shop_info.shop_phone;
					that.latitude = res.data.shop_info.latitude;
					that.longitude = res.data.shop_info.longitude;
					// 如果没有上传店铺详情页默认隐藏
					if(res.data.shop_info.shop_logo) {
						that.imgUrl = [
							that.baseURL + res.data.shop_info.shop_logo,
						];
						console.log(that.imgUrl)
					}
					else {
						that.swiperShow = false;
					}
					
					that.goods = res.data.goods;
					that.qr = res.data.qr;
				});
		},
		// 充值会员
		goMoney() {
			if(!this.isLogin) {
				uni.showToast({
					title: '请先登录',
					icon: 'none'
				});
				return
			}
			else {
				uni.navigateTo({
					url: '/pages/components/money/money'
				})
			}
		},
		// 咨询买单
		goRefer() {
			if(!this.isLogin) {
				uni.showToast({
					title: '请先登录',
					icon: 'none'
				});
				return
			}
			else {
				uni.navigateTo({
					url: '/pages/components/refer-member/refer-member?shop_id='+this.shop_id
				})
			}
			
		},
		// 商品详情
		goodsinfo(id) {
			uni.navigateTo({
				url: '/pages/common/goods-detail/goods-detail?id='+id
			})
		},
		// 预览店铺图
		preview() {
			uni.previewImage({
				urls: this.shopSrc
			})
		},
		// 打电话
		call() {
			if(this.shop_phone == '') {
				uni.showToast({
					title: '该商家暂未提供预约号码',
					icon: 'none'
				})
			}
			else {
				uni.makePhoneCall({
					phoneNumber: this.shop_phone
				})
			}
		}
	}
}
